@use '../../styles/mixins';
.prices {
  width: 100%;
  background-color: var(--main-color);

  &__wrapper {
    margin-top: 50px;
  }
}

.visuallyHidden {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}
